.button {
    font: inherit;
    border: 1px solid #23922e;
    background: #23922e;
    color: white;
    padding: 0.25rem 1rem;
    cursor: pointer;
  }
  
  .button:hover,
  .button:active {
    background: #23922e;
    border-color: #23922e;
  }
  
  .button:focus {
    outline: none;
  }